import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login/Login.vue'
import MainHome from '../views/MainHome/MainHome.vue'
import TuiJian from '../views/TuiJian/TuiJian.vue'


Vue.use(VueRouter)
const routes = [
    {
        path:'/',
        name:'Home',
        component: Home ,  //()=>import('../views/Home.vue)按需引入
        redirect: 'login',
        children:[
            {
                path:'login',
                name:'Login',
                component: Login
            }, {
                path:'mainhome',
                name:'MainHome',
                component: MainHome
            },
            {
                path:'tuijian',
                name:'TuiJian',
                component: TuiJian
            },

        ]
    }
]

const router =  new VueRouter({
    // mode:'hash',
    routes
})

export default router