<template>
  <div class="mainhome">
    <ul class="img">
      <li v-for="(item, index) in imgList" :key="index">
        <img :src="item" v-show="imgShow == index" />
      </li>
      <div class="spanlist" ref="spanall">
        <span v-for="(item, index) in imgList" :key="index"></span>
      </div>
    </ul>
    <div class="hx"></div>
    <div class="mainhome-list">
      <ul>
        <li>门店打卡</li>
        <li>机票</li>
        <li>火车票</li>
        <li>自驾游</li>
        <li>酒店</li>
        <li>特产</li>
        <li>国内游</li>
        <li>周边游</li>
        <li>跟团游</li>
      </ul>
    </div>
    <div class="footer-img">  <img src="./img/5.jpg"/></div>
  
  </div>
</template>

<script>
  import img1 from './img/1.jpg';
  import img2 from './img/2.jpg';
  import img3 from './img/3.jpg';
  import img4 from './img/4.jpg';

  export default {
    data() {
      return {
        imgShow: 0,
        imgList: [img1, img2, img3, img4],
      };
    },
    mounted() {
      this.imgLoop();
    },
    methods: {
      leftBtn() {
        let lis = this.$refs.spanall.querySelectorAll('span');
        for (let i = 0; i < lis.length; i++) {
          lis[i].className = '';
        }
        // 点击的元素变色
        lis[this.imgShow].className = 'active';
        this.imgShow++;
        if (this.imgShow > this.imgList.length - 1) {
          this.imgShow = 0;
        }
      },
      imgLoop() {
        let that = this;
        setInterval(() => {
          that.leftBtn();
        }, 3000);
      },
    },
  };
</script>

<style scoped>
  .mainhome {
    width: 100%;
  }
  .img {
    position: relative;
    height: 3rem;
  }
  .img li {
    width: 100%;
    height: 3rem;
    position: absolute;
  }
  .spanlist {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
  span {
    display: inline-block;
    width: 0.2rem;
    height: 0.2rem;
    border-radius: 50%;
    margin: 0 0.05rem;
    background-color: thistle;
  }
  .active {
    background: rgb(24, 57, 243);
  }
  img {
    width: 100%;
    height: 100%;
  }
  .hx {
    margin: 0.3rem;
    height: 0.03rem;
    background-color: thistle;
  }

  .mainhome-list {
    margin: 0.3rem;
    background-color: thistle;
  }

  .mainhome-list ul {
    display: flex;
    flex-wrap: wrap;
  }
  .mainhome-list ul li {
    display: flex;
    box-sizing: border-box;
    border-bottom: 1px solid white;
    width: 33.33%;
    height: 1rem;
    border-left: 1px solid white;
    justify-content: center;
    align-items: center;
    background-color: #ddd;
  }
  .footer-img {
      /* height: 2rem; */
      margin: .3rem;
  }
</style>
